import React, { useState, useEffect } from 'react';

const AddProduct = () => {
  const [categories, setCategories] = useState<any[]>([]);  // Trạng thái cho danh mục
  const [formData, setFormData] = useState({
    name: '',
    price: '',
    discountPrice: '',
    category: '',
    description: '',
    ingredients: '',
    usage_instructions: '',
    special: '',
    stock: '',
    images: [] as string[],
  });

  // Fetch categories from API
  useEffect(() => {
    const fetchCategories = async () => {
      const response = await fetch('https://api-zeal.onrender.com/api/categories');
      const data = await response.json();
      setCategories(data);
    };
    fetchCategories();
  }, []);

  // Handle form input change
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  // Handle file input change for images
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = Array.from(e.target.files);
      const fileNames = files.map(file => file.name);
      setFormData((prevState) => ({
        ...prevState,
        images: fileNames,
      }));
    }
  };

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const productData = {
      ...formData,
      category: formData.category, // Ensure the category is correctly set
    };

    const response = await fetch('https://api-zeal.onrender.com/api/products', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(productData),
    });

    if (response.ok) {
      alert('Sản phẩm đã được thêm thành công!');
      // Reset form data
      setFormData({
        name: '',
        price: '',
        discountPrice: '',
        category: '',
        description: '',
        ingredients: '',
        usage_instructions: '',
        special: '',
        stock: '',
        images: [],
      });
    } else {
      alert('Đã xảy ra lỗi khi thêm sản phẩm.');
    }
  };

  return (
    <div>
      <h1>Thêm sản phẩm</h1>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Tên sản phẩm</label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label>Giá</label>
          <input
            type="number"
            name="price"
            value={formData.price}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label>Giá khuyến mãi</label>
          <input
            type="number"
            name="discountPrice"
            value={formData.discountPrice}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label>Danh mục</label>
          <select
            name="category"
            value={formData.category}
            onChange={handleInputChange}
            required
          >
            <option value="">Chọn danh mục</option>
            {categories.map((category: any) => (
              <option key={category._id} value={category._id}>
                {category.name}
              </option>
            ))}
          </select>
        </div>
        <div>
          <label>Mô tả</label>
          <textarea
            name="description"
            value={formData.description}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label>Thành phần</label>
          <textarea
            name="ingredients"
            value={formData.ingredients}
            onChange={handleInputChange}
          />
        </div>
        <div>
          <label>Hướng dẫn sử dụng</label>
          <textarea
            name="usage_instructions"
            value={formData.usage_instructions}
            onChange={handleInputChange}
          />
        </div>
        <div>
          <label>Đặc điểm nổi bật</label>
          <textarea
            name="special"
            value={formData.special}
            onChange={handleInputChange}
          />
        </div>
        <div>
          <label>Số lượng</label>
          <input
            type="number"
            name="stock"
            value={formData.stock}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label>Hình ảnh</label>
          <input
            type="file"
            accept="image/*"
            multiple
            onChange={handleFileChange}
          />
        </div>
        <button type="submit">Thêm sản phẩm</button>
      </form>
    </div>
  );
};

export default AddProduct;
